import axios from 'axios';
// import UsersServiceAction from "../../actions/reduxActions";
import { reduxStore } from '../../reducers';
import UsersServiceActions from './usersActions';

const uuidv1 = require('uuid/v1');  // NOT for production. Only for presentation / mocking on github pages

const presentationMode = true; // im disabling some scopes. Im editing this file so it would work without the real connection to server and mongoDB. Just for presentation purposes on gh-pages
// i should have use .env variables... :|
class UsersServiceClass {
    async Add() {
        const mainFormData = reduxStore.getState().formDataReducer;
        // if (!presentationMode) {
        //     try {
        //         const response = await axios.post("http://localhost:3000/api/users/", mainFormData); // TODO: move to env

        //         const postedUserId = response.data;
        //         mainFormData.id = postedUserId;

        //     } catch (err) {
        //         console.log("axios.post error: ", err);
        //     }
        // }
        mainFormData.id = uuidv1();
        UsersServiceActions.addToUsersList(mainFormData);
    }

    async getUsers() {
        try {
            const response = await axios.get('http://localhost:3000/api/users/');
            if (response.status === 200) {
                UsersServiceActions.usersDataDownloaded(response.data);
            }
        } catch (err) {
            console.log('It is just a ghpages-presentation version. It is not connected to heroku /mongoDB. This button is dissabled');
        }
    }

    async deleteRecord(id) {
        if (!presentationMode) {
            try {
                const response = await axios.delete("http://localhost:3000/api/users/" + id)
                if (response.status === 200) {
                    UsersServiceActions.deleteFromUsersListByID(id)
                }
            } catch (err) {
                console.log('axios.delete error: ', err);
            }
        }
        UsersServiceActions.deleteFromUsersListByID(id)
    }
}

const UsersService = new UsersServiceClass();
export default UsersService;